<template>
  <div class="picture">
  <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
  </div>
  

  <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
  </van-row>

  <van-sticky>
    <van-field
    v-model="searchkey"
    center
    clearable
    left-icon="search"
    label="搜索房源"
    placeholder="请输入关键字">
        <template #button>
            <van-button size="small" type="info" @click="Search">搜索</van-button>
        </template>
    </van-field>


    <van-row>

    <van-col>
      <van-radio-group v-model="radio1" direction="horizontal">
        <van-radio name="1"><h3 style="color:black;">价格降序</h3></van-radio>
        <van-radio name="2"><h3 style="color:black;">价格升序</h3></van-radio>
      </van-radio-group>
    </van-col>

    <van-col>
      <van-radio-group v-model="radio2" direction="horizontal">
        <van-radio name="3"><h3 style="color:black;">面积降序</h3></van-radio>
        <van-radio name="4"><h3 style="color:black;">面积升序</h3></van-radio>
      </van-radio-group>
    </van-col>

    </van-row>
  </van-sticky> 

    <div>
        <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onload">
            <van-cell v-for="(item,index) in pictures" :key="index" clickable @click='toroom(item)' style="white-space: pre-wrap;">
                <img :src="require('../assets/'+pictures[index])" width="100px" height="100px" fit="contain">
                <span style="color:black;font-size:20px;margin-right:20px">地址：{{address[index]}}</span>
                <span style="color:black;font-size:20px;margin-right:20px">房间类型：{{type[index]}}</span>
                <span style="color:red;font-size:30px;margin-right:20px">月租：{{rent[index]}}元/月</span>
            </van-cell>
        </van-list>
    </div>

    <div>
    </div>

    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>

  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
import axios from 'axios';

export default {
  name: 'Renter',
  data () {
    return {
      msg: '顷刻',
      imgSrc:require('../assets/search.jpg'),
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      radio1:'0',
      radio2:'0',
      searchkey:'',
      count:'6',
	  
      pictures: [],
	  type: [],
	  rent: [],
	  address: [],
	  
      loading: false,
      finished: false,
    };
  },
  components:{
      Foot_bar
  },
  methods:{
      toroom(name){
          this.$router.push({
              path:'/Room',
              query:{roomname:name},
          });
      },
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      onload(){
          if(this.type.length==this.rent.length){
              this.finished=true;
          }
      },
      Search(){
          
		  var _this = this;
		  axios.get('/rentersearchroom',{
	        params: {
			  radio1: _this.radio1,
			  radio2: _this.radio2,
	     	  key: _this.searchkey,
		    }
          })
          .then(function (response){
		    console.log(response.data.pictures);
		    _this.count = response.data.num;
			
			_this.type = response.data.type;
			_this.rent = response.data.rent;
			_this.address = response.data.address;
			_this.pictures = response.data.pictures;
          })
          .catch(function (error) { // 请求失败处理
            console.log(error);
          });
      }
  },
  created(){
      this.id=this.$route.query.name;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1{
  font-weight: normal;
}
.background{
    width:100%;  
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    position: fixed;
}
 
.front{
    z-index:1;
    position: absolute;
}
</style>



