<template>
  <div class="CheckRoom">

      <van-sticky >

    <van-nav-bar
    title="顷刻"
    left-text="返回"
    :right-text="myid"
    left-arrow
    @click-left="onClickLeft"
    @click-right="onClickRight"/>

  </van-sticky>
    <h1>{{ msg1 }}</h1>
    <h2 style="color:red;">{{days}}天</h2>

    <p>
        <p><van-cell  size="large" title="查询本月需交费用" is-link arrow-direction="down"/></p>
    </p>

    <div>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
    </div>

    <div>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
    </div>
    

  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'CheckRoom',
  data () {
    return {
      msg1: '距离租约到期还有：',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      days:'100',
      msg2:'本月应交'
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid=="请先登入"){
              this.islogin="/";
          }
      },
      onClickLeft() {
          this.$router.push({
            path:'/MyInfo',
          });
      },
      onClickRight() {
          this.$router.push({
            path:'/MyInfo',
          });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



