<template>
  <div style="margin-top:66px;">
    <router-view/>
    <van-tabbar route v-model="active">
        <van-tabbar-item replace icon="home-o" to="/Renter">首页</van-tabbar-item>
        <van-tabbar-item  replace to="/MyInfo">
            <span>个人中心</span>
            <template #icon="props">
            <img :src="props.active ? icon.active : icon.inactive" />
            </template>
        </van-tabbar-item>
        <van-tabbar-item replace icon="search" to="/Search">搜索</van-tabbar-item>
        <van-tabbar-item replace icon="setting-o" to="/ConnectService">联系客服</van-tabbar-item>
        <van-tabbar-item replace icon="setting-o" to="/Setting">页面设置</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: 'Foot_bar',
  data () {
    return {
        active: 0,
        icon: {
            active: 'https://img.yzcdn.cn/vant/user-active.png',
            inactive: 'https://img.yzcdn.cn/vant/user-inactive.png',
        },
    }
  }
}
</script>

<style scoped>
</style>



